import React from 'react';
import { Route, Switch } from 'react-router-dom';
import DashboardPage from './pages/DashboardPage';
import UsagePage from './pages/UsagePage';
import AboutPage from './pages/AboutPage';
import UtilityPage from './pages/UtilityPage';
import NotFoundPage from './pages/NotFoundPage';

class Routes extends React.Component {
  componentDidMount(){
    // this.props.setDashboard("HARRO");
  }

  render() {
    return (
      <Switch>
        
        <Route path='/' exact render={() => <DashboardPage setDashboard={this.props.setDashboard} />} />
        <Route path='/dashboard' exact render={() => <DashboardPage setDashboard={this.props.setDashboard} />} />
        <Route path='/usage' exact render={() => <UsagePage setDashboard={this.props.setDashboard} />} />
        <Route path='/utility' exact render={() => <UtilityPage setDashboard={this.props.setDashboard} />} />
        <Route path='/about' exact render={() => <AboutPage setDashboard={this.props.setDashboard} />} />
        <Route path='/404' exact render={() => <NotFoundPage setDashboard={this.props.setDashboard} />} />
      </Switch>
    );
  }
}

export default Routes;
